<template>
  <a
    :class="classes"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <slot></slot> 
  </a>
</template>

<script>
  export default {
    name: 'Link',
    props: {
      variant: { 
        type: String, 
        default: 'primary' 
      }
    },
    computed: {
      classes() {
        return [
          'link',
          `link-${this.variant}`
        ]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .link {
    display: inline-flex;
    align-items: center;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;

    &:active {
      opacity: .8;
    }

    &-primary {
      color: $blue;
    }
  }
</style>