<template>
  <select 
    class="select"
    v-bind="{...$attrs}"
    v-on="{...$listeners}"
    :value="selected"
    @input="event => $emit('update:selected', event.target.value)"
  >
    <option 
      v-for="option in options" 
      :key="option" 
      :value="option">
      {{ option }}
    </option>
  </select>
</template>

<script>
  export default {
    name: 'Select',
    props: {
      selected: {
        type: String,
        default: ''
      },
      options: {
        type: Array,
        default: []
      }
    },
  }
</script>

<style lang="scss" scoped>
  .select {
    appearance: none;
    outline: none;
    border: 0;
    display: inline-flex;
    align-items: center;
    background-color: $gray-neutral;
    border-radius: 15px;
    height: 55px;
    padding: 0 30px;
    font-size: 14px;
    font-weight: 500;
    color: $black;
    outline: none;
  }
</style>