<template>
  <div class="tooltip">
    <div class="tooltip-button">
      <slot name="button"></slot>
    </div>
    
    <div class="tooltip-popup">
      <slot name="text"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Tooltip'
}
</script>

<style lang="scss" scoped>
  .tooltip {
    position: relative;
    display: inline-flex;

    &-button {
      width: 100%;
      flex-shrink: 0;
      cursor: pointer;
      display: inline-flex;

      &:hover {
        & + .tooltip-popup {
          opacity: 1;
        }
      }
    }

    &-popup {
      position: absolute;
      opacity: 0;
      top: 50%;
      transform: translateY(-50%);
      left: calc(100% + 10px);
      width: 230px;
      padding: 30px 40px;
      background-color: $black;
      box-shadow: 0 10px 25px rgba($black, .1);
      color: $white;
      z-index: 1;
      border-radius: 30px;
      transition: opacity .2s $transition-function;
    }
  }
</style>