<template>
  <div class="config">
    <header class="config-header">
      <Link>
        <IconChevronLeft style="margin-right: 15px"></IconChevronLeft>
        Back
      </Link>
      <img 
        class="config-logo" 
        width="116" 
        alt="Youtube logo" 
        src="../assets/images/youtube-icon.svg">

      <h1 class="config-title">Name of Youtube</h1>

      <Button class="config-action">
        Save
        <IconCheck style="margin-left: 25px"></IconCheck>
      </Button>
    </header>

    <main class="config-main">
      <aside class="config-sidebar">
        <FormControl label="Youtube URL">
          <Input 
            v-model="config.url"
            @input="value => loadUrl(value)"
            tip="Accepted formats include: www.youtube.com, m.youtube.com, youtu.be"
            placeholder="www.youtube.com">
          </Input>
        </FormControl>

        <div class="togglers">
          <Toogler 
            v-model="playerVars.mute"
            @input="value => !value ? player.unMute() : player.mute()"
            label="Mute Video">
          </Toogler>
          <Toogler 
            v-model="config.muted"
            label="Toggle subtitles">
          </Toogler>
        </div>

        <FormControl label="Volume">
          <RangeSlider 
            :model.sync="config.volume"
            @update:model="value => player.setVolume(value)">
          </RangeSlider>
        </FormControl>

        <FormControl label="Max number of videos to play within a YouTube playlist (optional)">
          <Input 
            v-model="config.max"
            tip="Accepted numbers only"
            placeholder="e.g. 10">
          </Input>
        </FormControl>
      </aside>

      <div class="config-content">
        <div class="preview">
          <youtube
            style="width: 100%; height: 100%; border: 10px solid #14121f"
            ref="youtube"
            :player-vars="playerVars"
            @playing="playing"
            @ready="ready">
          </youtube>
        </div>
      </div>

    </main>
  </div>
</template>

<script>
  import Link from '@/components/ui/Link.vue';
  import Button from '@/components/ui/Button.vue';
  import Input from '@/components/ui/controls/Input.vue';
  import RangeSlider from '@/components/ui/controls/RangeSlider.vue';
  import Toogler from '@/components/ui/controls/Toogler.vue';
  import FormControl from '@/components/ui/FormControl.vue'
  import IconCheck from '@/assets/icons/check.svg';
  import IconChevronLeft from '@/assets/icons/chevron-left.svg';
  import Select from '@/components/ui/controls/Select.vue';

  import urlParser from 'js-video-url-parser';

  export default {
    name: 'Config',
    components: {
      Link,
      Button,
      Input,
      RangeSlider,
      Toogler,
      Select,
      FormControl,
      IconCheck,
      IconChevronLeft
    },
   
    data() {
      return {
        playerVars: {
          autoplay: 1,
          controls: 0,
          disablekb: 1,
          fs: 0,
          loop: 1,
          modestbranding: 1,
          rel: 0,
          showinfo: 0,
          mute: 0,
          autohide: 1
        },
        config: {
          url: null,
          muted: 0,
          volume: 100,
          max: null
        }
      }
    },
    methods: {
      async loadUrl(value) {
        this.player.stopVideo();

        const data = urlParser.parse(value);

        if (data) {
          if (data.list) {
            this.loadPlaylist(data.list);
            return;
          }
          if (data.id) {
            this.loadVideo(data.id);
            return;
          }
        }

        else {
          if (value.length === 11 ) {
            this.loadVideo(value);
            return;
          }
          if (value.length > 11) {
            this.loadPlaylist(value);
            return;
          }
        }
      },
      loadVideo(videoId) {
        try {
          this.player.loadVideoById({ videoId });
        } catch (error) {
          console.warn('Error: Playlist not finded!');
        }
      },
      loadPlaylist(list) {
        try {
          this.player.loadPlaylist({ list });
        } catch (error) {
          console.warn('Error: Playlist not finded!');
        }
      },
      async playing() {
        //
      },
      async ready() {
        //
      }
    },
    computed: {
      player() {
        return this.$refs.youtube.player;
      }
    }
  }
</script>

