<template>
  <div class="range-slider">
    <input 
      class="range-slider-input" 
      type="range" 
      min="0" 
      max="100"
      v-bind="$attrs"
      v-on="$listeners"
      :value="model" 
      @change="$emit('update:model', Number($event.target.value))"
      @input="$emit('update:model', Number($event.target.value))"/>
	  <output 
      :style="{ width: '4ch'}" 
      class="range-slider-output">
      {{`${model}%`}}
    </output>
  </div>
</template>

<script>
  export default {
    name: 'RangeSlider',
    props: {
      model: {
        type: Number,
        default: 0
      }
    }
  }
</script>

<style lang="scss" scoped>
  .range-slider {
    display: flex;
    align-items: center;
    padding: 0 30px;
    height: 20px;

    &-output {
      color: $blue;
      padding-left: 20px;
      text-align: right;
    }
  }

  input[type="range"] {
    -webkit-appearance: none;
    width: 100%;
    height: 2px;
    background: $black;
    border: 0;
    border-radius: 0;
  }
  

  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 23px;
    height: 23px;
    background: $white;
    border-radius: 30px;
    border: 3px solid $white;
    box-shadow: none;
    transition: all 0.5s ease;
    box-shadow: inset 0 0 0 2px $black;
    cursor: pointer;
    transition: all .2s $transition-function;
  }
</style>