<template>
  <div :class="classes">
    <label class="toggler-label" :for="_uid">
      <input
        class="toggler-input"
        :id="_uid"
        v-bind="{...$attrs}"
        type="checkbox"
        :checked="$attrs.value"
        @change="event => $emit('input', event.target.checked ? 1 : 0)"
      >
      <span class="toggler-text">{{label}}</span>
    </label>
  </div>
</template>

<script>
  export default {
    name: 'Toggler',
    props: {
      label: { 
        type: String, 
        default: '' 
      }
    },
    computed: {
      classes() {
        return [
          'toggler',
          this.$attrs.value ? 'toggler-checked' : null
        ]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .toggler {
    display: block;
    background-color: $gray-neutral;
    border-radius: 15px;
    font-size: 14px;
    font-weight: 500;
    color: $gray;
    outline: none;

    &-checked {
      color: $white;
      background-color: $black;
      transition: all .1s $transition-function;

      .toggler-label {
        &::before {
          border-color: $white;
          background: $white;
          box-shadow: inset 0 0 0 2px $black;
          transition: all .1s $transition-function;
        }
      }
    }

    &-input {
      opacity: 0;
      width: 0;
      height: 0;
    }

    &-label {
      width: 100%;
      display: inline-flex;
      align-items: center;
      padding: 0 30px;
      height: 55px;
      position: relative;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        width: 17px;
        height: 17px;
        border-radius: 5px;
        border: 1px solid $gray;
        box-shadow: none;
        background-color: transparent;
      }
    }

    &-text {
      padding-left: 40px;
      user-select: none;
    }
  }
</style>