<template>
  <div class="form-control">
    <label
      v-if="label"
      class="form-control-label" 
      :for="_uid">
      {{label}}
    </label>
    <slot :id="_uid"></slot>
  </div>
</template>

<script>
  export default {
    name: 'FormControl',
    props: {
      label: { 
        type: String, 
        default: '' 
      }
    },
  }
</script>

<style lang="scss" scoped>
  .form-control {
    display: flex;
    flex-direction: column;

    &-label {
      padding-left: 30px;
      font-weight: bold;
      color: $black;
      margin-bottom: 20px;
    }
  }
</style>