<template>
  <button
    :class="classes"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <slot></slot> 
  </button>
</template>

<script>
  export default {
    name: 'Button',
    props: {
      variant: { 
        type: String, 
        default: 'primary' 
      }
    },
    computed: {
      classes() {
        return [
          'button',
          `button-${this.variant}`
        ]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .button {
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    border: 0;
    border-radius: 9999px;
    height: 55px;
    padding: 0 45px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;

    &:active {
      opacity: .8;
    }

    &-primary {
      color: $white;
      background-color: $black;
    }
  }
</style>