<template>
  <div class="input">
    <input
      class="input-control"
      v-bind="{...$attrs}"
      v-on="{...$listeners, input: event => $emit('input', event.target.value)}"
    >
    <div class="input-actions">
      <Tooltip v-if="tip">
        <template #button>
          <IconInfo width="15"></IconInfo>
        </template>
        <template #text>{{tip}}</template>
      </Tooltip>
    </div>
  </div>
</template>

<script>
  import Tooltip from '@/components/ui/Tooltip.vue';
  import IconInfo from '@/assets/icons/info.svg';

  export default {
    name: 'Input',
    components: {
      Tooltip,
      IconInfo
    },
    props: {
      tip: {
        type: String
      }
    }
  }
</script>

<style lang="scss" scoped>
  .input {
    display: inline-flex;
    align-items: center;
    position: relative;

    &-control {
      flex: 1;
      background-color: $gray-neutral;
      border: 0;
      border-radius: 15px;
      height: 55px;
      padding: 0 75px 0 30px;
      font-size: 14px;
      font-weight: 500;
      color: $black;
      outline: none;
    }

    &-actions {
      max-width: 75px;
      display: flex;
      align-items: center;
      position: absolute;
      right: 0;
      height: 100%;
      top: 0;
      padding: 0 30px;
      z-index: 1;
    }

    &::placeholder {
      color: $gray;
      font-size: 14px;
    }
  }
</style>